body {
  color: #333;
  background-color: #f8f8f8;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.products {
  text-align: center;
  padding: 50px 20px;
}

.products__title {
  color: #e39746;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 30px;
  font-size: 2.5rem;
}

.products__box {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.products__list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.products__item {
  color: #fff;
  background: linear-gradient(135deg, #e39746, #86b8b3);
  border-radius: 15px;
  width: 280px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 5px 15px #0000001a;
}

.products__item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px #0003;
}

.products__wrapper {
  border-bottom: 2px solid #ffffff80;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding-bottom: 10px;
  display: flex;
}

.products__subtitle {
  text-align: left;
  font-size: 1.5rem;
  font-weight: bold;
}

.products__price {
  background: #fff3;
  border-radius: 10px;
  padding: 5px 10px;
  font-size: 1.2rem;
  font-weight: bold;
}

.products__desc {
  opacity: .9;
  font-size: 1rem;
  line-height: 1.5;
}

.products__item:after {
  content: "";
  background: #ffffff1a;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  position: absolute;
  top: -20px;
  right: -20px;
}
